
.containerLeft{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: start;
      align-items: flex-start;
  width: 100%;
}

.containerLeft__date{
  color: var(--coolGrey);
  letter-spacing: 9px;
  font-weight: 600;
  font-size: 14px;
  text-transform: uppercase;
}

.containerLeft__title{
  font-size: 36px;
  letter-spacing: 1.5px;
  font-weight: 600;
  margin-top: 15px;
}

.containerLeft__timer{
  font-size: 14px;
  font-weight: 400;
}
.containerLeft__mission_patch{
  margin-top: 30px;
}

.mission_patch--nasa{
  width: 65%;
  height: auto;
}



